.root {
  // background: #0bac4e;
  // padding: 10px;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  :global(.material-icons) {
    color: #fff;
    color: #fff5e1;
  }
}

:global(.my-div-icon) {
  // position: absolute;
  width: 30px;
  height: 30px;
  background: yellow;
  border: 5px solid red;
}
.marker {
  // background: #fff;
  display:flex;
  align-items: center;
  justify-content: center;
  span {
    font-size: 36px;
    color: #fff5e1;
    color:red;
    text-shadow: 0 2px 2px rgba(0,0,0,0.5);
    &:global(.icon-telephone) {
      color:blue;
    }
    &:global(.icon-house) {
      color:green;
    }
    &:global(.icon-money-bag) {
      color:yellow;
    }
  }
}