}
.leaflet-container {
  /* background: #ddd; */
  background: transparent !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'icomoon';
  src:  url(/koholint/static/media/icomoon.9c6277f2.eot);
  src:  url(/koholint/static/media/icomoon.9c6277f2.eot#iefix) format('embedded-opentype'),
    url(/koholint/static/media/icomoon.c41a9559.ttf) format('truetype'),
    url(/koholint/static/media/icomoon.f9afade2.woff) format('woff'),
    url(/koholint/static/media/icomoon.0924dfed.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

